.button {
  font-weight: 300;
  color: rgb(35, 159, 197);
  font-size: 1.2em;
  text-decoration: none;
  border: 1px solid #efefef;
  padding: .5em;
  border-radius: 3px;
  float: left;
  margin: 6em 0 0 -155px;
  left: 50%;
  position: relative;
  transition: all .3s linear;
}

.button:hover {
  background-color: #007aff;
  color: rgb(202, 215, 226);
}

.four_o_four > p {
  font-size: 2em;
  text-align: center;
  font-weight: 100;
}

