.gridContainer {
  display: grid;
  grid-template-columns: auto 60px;
  background: #f1570f96;
  border-radius: 5px;
  margin: 20px 0 20px 0;
}

.gridContainer .titleBar {
  padding-left: 10px;
  /* padding-bottom: 10px; */
}

.chart-grid {
  display: grid;
  grid-template-columns: 1.05fr 1fr;
  border-radius: 5px;
  /* margin: 20px; */
}

/* Tooltip CSS */

.tooltip {
  position: relative;
  display: inline-block;
  margin-left: -30px;
  color: rgba(29, 24, 24, 0.781);
  /* border-bottom: 1px dotted black; */
}

.tooltip .tooltiptext {
  visibility: hidden;
  background-color: rgb(255, 255, 255);
  color: rgb(14, 13, 13);
  text-align: left;
  border-radius: 6px;
  padding: 0px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext {
  min-width: 300px;
  bottom: 100%;
  padding: 20px;
  left: 50%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  /* margin-left: -5px; */
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.tooltip .tooltiptext {
  opacity: 0;
  transition: opacity 1s;
}

.tooltip:hover .tooltiptext {
  opacity: 1;
}
